.Footer {
    color: #fff;
    margin-top: 20px;
    font-size: 14px;
    background: #000;
}

.FooterBottomToTop {
    background: #232f3e;
    margin-bottom: 40px;
    text-align: center;
}

.FooterBottomToTop:hover {
    cursor: pointer;
    background: #3368a9;
}

