.home {
    max-width: 1500px;
    margin: 0 auto;
}

.homeSliderContainer {
    width: 100%;
    margin: 0 auto;
    height: 40vh;
    position: relative;
    border-radius: 0.5rem;
}

.homeSlide {
    position: absolute;
    width: 100%;
    display: grid;
    place-items: center;
    transition: 0.2s ease-in-out all;
    text-align: center;
}

.homeImg {
    width: 100%;
    height: 500px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1),
    rgba(0,0,0,0));
    -webkit-mask-image: linear-gradient(to bottom, rgba(0,0,0,1),
    rgba(0,0,0,0));
    z-index: -1;
    margin-bottom: -300px;
    transition: 0 all ease-in-out;
}

.HomeRow {
    padding-top: 70px;
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}